'use strict';
import { LOAN_PICTURE} from '@/config/image.js';
import { LOAN_APPLICATION, LOAN_TESTONETEST} from '@/config/router.js';
var _self;
export default {
	data() {
		return{
			img:LOAN_PICTURE,
			show:false,
			
		}
	},
	methods:{
		imgshow(){
			_self.show=true
		},
		submitTest(){
			uni.navigateTo({
				url:LOAN_TESTONETEST
			})
		},
		submitApply(){
			uni.navigateTo({
				url:LOAN_APPLICATION
			})
		},
	},
	async onLoad() {
		_self=this
	}
};
